"use strict";
// TODO: React-Router + integration with redux?
// https://github.com/rackt/redux/issues/291
// http://rackt.org/redux/docs/advanced/AsyncActions.html
var redux_actions_1 = require('redux-actions');
var Firebase = require('firebase');
var ActionTypes_1 = require('../constants/ActionTypes');
var main_1 = require('../main');
var dbRoot = new Firebase('https://mhfi-poker.firebaseio.com/');
var sessions = dbRoot.child('sessions');
var orderedSessions = sessions.orderByChild('name');
var users = dbRoot.child('users');
var curUserList;
orderedSessions.on('child_added', function (snapshot, prevSessionId) {
    var session = snapshot.val();
    //console.log('child_added', session, prevChildKey);
    main_1.store.dispatch({
        type: ActionTypes_1.SessionAction.Add,
        payload: { session: session, prevSessionId: prevSessionId }
    });
});
orderedSessions.on('child_changed', function (snapshot) {
    var session = snapshot.val();
    //console.log('child_changed', session);
    main_1.store.dispatch({
        type: ActionTypes_1.SessionAction.Change,
        payload: snapshot.val()
    });
});
orderedSessions.on('child_removed', function (snapshot) {
    var session = snapshot.val();
    //console.log('child_removed', session);
    main_1.store.dispatch({
        type: ActionTypes_1.SessionAction.Remove,
        payload: snapshot.val().sessionId
    });
});
// orderedSessions.on('child_moved', (snapshot, prevChildKey) => {
//   let session = snapshot.val();
//   console.log('child_moved', session, prevChildKey);
// });
var login = function () {
    return function (dispatch) {
        var handleAuth = function (authData) {
            var _a = authData[authData.provider], displayName = _a.displayName, profileImageURL = _a.profileImageURL;
            dispatch({
                type: ActionTypes_1.UserAction.Auth,
                payload: {
                    userId: authData.uid,
                    name: displayName,
                    avatarUrl: profileImageURL
                }
            });
        };
        var auth = dbRoot.getAuth();
        if (auth) {
            handleAuth(auth);
        }
        else {
            dbRoot.authWithOAuthPopup('google', function (error, authData) {
                if (error) {
                    console.error('Authentication Error', error);
                }
                else {
                    //console.log('Authenticated: ', authData);
                    handleAuth(authData);
                }
            });
        }
    };
};
exports.login = login;
var editSession = redux_actions_1.createAction(ActionTypes_1.SessionAction.Change, function (sessionId, name, desc) {
    var session = {
        sessionId: sessionId, name: name, desc: desc,
        lastUpdated: new Date()
    };
    sessions.child(sessionId).update(session);
    return session;
});
exports.editSession = editSession;
var removeSession = redux_actions_1.createAction(ActionTypes_1.SessionAction.Remove, function (sessionId) {
    users.child(sessionId).remove();
    sessions.child(sessionId).remove();
    return sessionId;
});
exports.removeSession = removeSession;
var joinSession = function (sessionId, user) {
    return function (dispatch) {
        var sessionUsers = users.child(sessionId);
        var orderedUsers = sessionUsers.orderByChild('name');
        orderedUsers.on('child_added', function (snapshot, prevUserId) {
            var user = snapshot.val();
            //console.log('child_added (user)', user);
            dispatch({
                type: ActionTypes_1.UserAction.Add,
                payload: { user: user, prevUserId: prevUserId }
            });
        });
        orderedUsers.on('child_changed', function (snapshot) {
            var user = snapshot.val();
            dispatch({
                type: ActionTypes_1.UserAction.Change,
                payload: user
            });
        });
        orderedUsers.on('child_removed', function (snapshot) {
            var removedUser = snapshot.val();
            dispatch({
                type: ActionTypes_1.UserAction.Remove,
                payload: removedUser
            });
        });
        sessionUsers.once('value', function (snapshot) {
            if (!snapshot.exists()) {
                // this user is the first one into an empty session, so make them an adminUser
                sessions.child(sessionId).update({ adminUser: user.userId });
            }
            // either way, add the user to the session
            var userData = (_a = {},
                _a[user.userId] = user.toJS(),
                _a
            );
            sessionUsers.update(userData);
            var _a;
        });
        sessions.child(sessionId).once('value', function (snapshot) {
            dispatch({
                type: ActionTypes_1.SessionAction.Join,
                payload: snapshot.val()
            });
        });
        window.onbeforeunload = function (e) {
            sessionUsers.child(user.userId).remove();
        };
    };
};
exports.joinSession = joinSession;
var addSession = function (name, desc, user) {
    return function (dispatch) {
        var newRef = sessions.push();
        var session = {
            sessionId: newRef.key(),
            name: name, desc: desc,
            lastUpdated: new Date(),
            adminUser: user.userId
        };
        newRef.set(session, function (error) {
            if (error) {
                console.error(error);
            }
            else {
                joinSession(session.sessionId, user)(dispatch);
            }
        });
    };
};
exports.addSession = addSession;
var leaveSession = redux_actions_1.createAction(ActionTypes_1.SessionAction.Leave, function (sessionId, userId) {
    var sessionUsers = users.child(sessionId);
    var orderedUsers = sessionUsers.orderByChild('name');
    // this works, even though it's not the same instance that 'on' was called from
    orderedUsers.off('child_added');
    orderedUsers.off('child_changed');
    orderedUsers.off('child_removed');
    window.onbeforeunload = null;
    sessionUsers.child(userId).remove();
    return sessionId;
});
exports.leaveSession = leaveSession;
var setVote = redux_actions_1.createAction(ActionTypes_1.VoteAction.Set, function (sessionId, userId, vote) {
    users.child(sessionId + "/" + userId)
        .update({ vote: vote });
    return { sessionId: sessionId, userId: userId, vote: vote };
});
exports.setVote = setVote;
var clearVotes = redux_actions_1.createAction(ActionTypes_1.VoteAction.ClearAll, function (sessionId) {
    users.child(sessionId)
        .transaction(function (current) {
        Object.keys(current).forEach(function (key) {
            // used to set this to null, but Firebase
            // doesn't send null keys, so the merge didn't
            // remove the vote client-side
            current[key].vote = -100;
        });
        return current;
    });
    sessions.child(sessionId).update({ votesRevealed: false });
});
exports.clearVotes = clearVotes;
var revealVotes = redux_actions_1.createAction(ActionTypes_1.VoteAction.Reveal, function (sessionId) {
    sessions.child(sessionId).update({ votesRevealed: true });
});
exports.revealVotes = revealVotes;



